import React, { Component, useMemo } from 'react';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome, __IST__ } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import { BACKEND_GENERIC_LOGGER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { requestHeaderGETWithAuth, requestHeaderPUTWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
} from '@coreui/react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import MaterialReactTable from 'material-react-table';
import moment from 'moment';
import CIcon from '@coreui/icons-react';
import { Table } from 'react-bootstrap';

//default
const FILE_NAME = "OfficeTimeTracker.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    data: null,
    totalTimeInvestedInMin: 987
};


const OfficeTimeDataType = {
    START_TIME : "START_TIME",
    END_TIME:"END_TIME",
    TIME_TAKEN:"TIME_TAKEN",
    TITLE:"TITLE",
    METADATA:"METADATA"
};

class OfficeTimeTracker extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchEnumDataList();
    }

    //default
    componentDidMount() {
        document.title = "Logger - Office";
        this.fetchEnumDataList();
    }

    fetchEnumDataList = () => {
        this.setState({
            data: {
                date: "2025-01-07",
                day: "TUE",
                events: [
                    {
                        startTime: "0900",
                        endTime: "1000",
                        timeTakenInMin: 60,
                        title: "[0] plan the day",
                        metadata: null
                    },
                    {
                        startTime: "1000",
                        endTime: "1015",
                        timeTakenInMin: 15,
                        title: "[1] MDP standup",
                        metadata: null
                    },
                    {
                        startTime: "1015",
                        endTime: "1045",
                        timeTakenInMin: 30,
                        title: "[2] Code review",
                        metadata: null
                    },
                    {
                        startTime: "1045",
                        endTime: "1100",
                        timeTakenInMin: 15,
                        title: "[3] location standup",
                        metadata: null
                    }
                ]
            }
        }, () => {
            const { data } = this.state;
            let tempData = data;
            const totalTimeInvestedInMin = this.calculateTotalTimetaken(tempData.events);
            this.setState({
                isLoaded: true,
                totalTimeInvestedInMin: totalTimeInvestedInMin
            });
        });
    }

    calculateTotalTimetaken = (eventsList) => {
        const totalTimeInvestedInMin = eventsList.reduce((accumulator, event) => accumulator + event.timeTakenInMin, 0);
        return totalTimeInvestedInMin;
    }

    updateValue = (type, event, index) => {
        const updatedData =  event.target.value;
        console.log("updateStartTime : ",type, " ~ ", index, " ~ ", updatedData);

        this.setState({
            // isLoaded: false
        }, () => {
            const { data } = this.state; // ???
            let tempEvents = data.events;

            const eventsToBeUpdated = tempEvents[index];

            if(type === OfficeTimeDataType.START_TIME) {
                eventsToBeUpdated.startTime = updatedData;
            } else if(type === OfficeTimeDataType.END_TIME) {
                eventsToBeUpdated.endTime = updatedData;
            } else if(type === OfficeTimeDataType.TIME_TAKEN) {
                eventsToBeUpdated.timeTakenInMin = updatedData;
            } else if(type === OfficeTimeDataType.TITLE) {
                eventsToBeUpdated.title = updatedData;
            } else if(type === OfficeTimeDataType.METADATA) {
                eventsToBeUpdated.metadata = updatedData;
            }

            this.setState({
                data,
                isLoaded: true
            });

        });
        
    }

    deleteRow = (index) => {
        this.setState({
            isLoaded: false
        }, () => {
            const { data } = this.state; // ???
            let tempEvents = data.events;
    
            const newUpdatedEventsArray = [];
            for(let i = 0 ; i < tempEvents.length ; i++) {
                if(i !== index)
                    newUpdatedEventsArray.push(tempEvents[i]);
            }
    
            data.events = [...newUpdatedEventsArray];
    
            this.setState({
                data,
                isLoaded: true
            });
        });
    }

    createNewEventRow = (index) => {
        this.setState({
            isLoaded: false
        }, () => {
            const { data } = this.state; // ???
            let tempEvents = data.events;
            let newEvent = {
                startTime: null,
                endTime: null,
                timeTakenInMin: null,
                title: null,
                metadata: null
            };
            if(index === undefined) {
                tempEvents.push(newEvent);
            } else {
                tempEvents.splice(index, 0, newEvent);
            }
            
            this.setState({
                isLoaded: true,
                data
            });
        });
    }

    updateData = () => {

    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ?
                <div>
                    <p> HELLO </p>
                    <CButton color="success" className="px-4" onClick={() => this.createNewDay()}>Create new day</CButton>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <CContainer>
                        <CRow className="justify-content-center">
                            <CCol md="8">
                                <CCardGroup>
                                    <CCard className="p-4">
                                        <CCardBody>
                                            <CInput
                                                placeholder="Date Of Purchase"
                                                autoComplete="on"
                                                type="date"
                                                defaultValue={new moment(this.state.data.date).format('YYYY-MM-DD')} //2020-03-08
                                                step="any"
                                                id="inputDateOfPurchase"
                                                label="Date Of Purchase"
                                                required
                                            />
                                            <br />
                                            <b>{this.state.data.day}</b>
                                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                            <b>TOTAL TIME invested : </b>{this.state.totalTimeInvestedInMin} min.
                                        </CCardBody>
                                    </CCard>
                                </CCardGroup>
                            </CCol>
                        </CRow>
                    </CContainer>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;

                    
                        <Table>
                            <thead>
                                <tr style={{
                                    position: 'sticky',
                                    background: 'white',
                                    top: 0
                                }}>
                                    <th style={{width : "1%"}}>#</th>
                                    <th style={{width : "1%"}}>startTime</th>
                                    <th style={{width : "1%"}}>endTime</th>
                                    <th style={{width : "1%"}}>timetaken</th>
                                    <th>title</th>
                                    <th>metadata</th>
                                    <th style={{width : "1%"}}>actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.data.events.map(
                                        (event, index) =>
                                            <tr key={"event_id_" + index}>
                                                <td>{index + 1}</td>
                                                <td>
                                                    <CInput
                                                        placeholder="tags üè∑Ô∏è üîñ (not-mandatory)"
                                                        defaultValue={event.startTime}
                                                        autoComplete="on"
                                                        step="any"
                                                        id="inputTags"
                                                        label="expense-tags"
                                                        required={false}
                                                        onChange={(event) => this.updateValue(OfficeTimeDataType.START_TIME, event, index)}
                                                    />
                                                </td>
                                                <td>
                                                    <CInput
                                                        placeholder="tags üè∑Ô∏è üîñ (not-mandatory)"
                                                        defaultValue={event.endTime}
                                                        autoComplete="on"
                                                        step="any"
                                                        id="inputTags"
                                                        label="expense-tags"
                                                        required={false}
                                                        onChange={(event) => this.updateValue(OfficeTimeDataType.END_TIME, event, index)}
                                                    />
                                                </td>
                                                <td>
                                                    <CInput
                                                        placeholder="tags üè∑Ô∏è üîñ (not-mandatory)"
                                                        defaultValue={event.timeTakenInMin}
                                                        autoComplete="on"
                                                        step="any"
                                                        id="inputTags"
                                                        label="expense-tags"
                                                        required={false}
                                                        onChange={(event) => this.updateValue(OfficeTimeDataType.TIME_TAKEN, event, index)}
                                                    />
                                                </td>
                                                <td>
                                                    <CInput
                                                        placeholder="tags üè∑Ô∏è üîñ (not-mandatory)"
                                                        defaultValue={event.title}
                                                        autoComplete="on"
                                                        step="any"
                                                        id="inputTags"
                                                        label="expense-tags"
                                                        onChange={(event) => this.updateValue(OfficeTimeDataType.TITLE, event, index)}
                                                    />
                                                </td>
                                                <td>
                                                    
                                                    <CTextarea 
                                                        placeholder='Some extra notes üìù (not-mandatory)' 
                                                        id="inputExtraNotes"
                                                        defaultValue={event.metadata}
                                                        rows="3"
                                                        onChange={(event) => this.updateValue(OfficeTimeDataType.METADATA, event, index)}
                                                    />
                                                </td>
                                                <td>
                                                    <CButton alt='create new row' color="info" className="px-4" onClick={() => this.createNewEventRow(index)}>‚ûï ‚§¥Ô∏è</CButton>
                                                    <CButton alt='delete row' color="warning" className="px-4" onClick={() => this.deleteRow(index)}>‚ùå</CButton>
                                                </td>
                                            </tr>
                                    )}
                            </tbody>
                        </Table>
                        <hr />
                        <CButton alt='create new row' color="info" className="px-4" onClick={() => this.createNewEventRow()}>‚ûï ‚Ü©Ô∏è</CButton>
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <CButton alt='create new row' color="success" className="px-4" onClick={() => this.updateData()}>SAVE</CButton>

                </div> : <Loading />
        );
    }

}

export default OfficeTimeTracker;
